.header {
    height:365px;
    width:100%;
    background-image: url("professional-programmer-working-late-dark-office.jpg");
    background-position-y:center;
    background-size: cover;
}

.header h1 {
    color:white;
    font-weight: lighter;
}

.overlay {
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height:100%;
    display: flex;
    flex-direction: column;
}

.h1-and-navbar {
    padding:15px;
    width:60%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}

.nav-bar a { 
    text-decoration: none;
    color:rgb(212, 211, 211);
    padding:0 15px;
    font-size: small;
}

.header .nav-bar > a:focus, a:hover {
    color:white;
}

.welcome {
    text-align: center;
    position:relative;
    top:80px;
}