.pc-container {
    width:50%;
    margin: 0 auto;
    padding:30px;
}

.pc {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap:10px;
}
.pc .pc-item {
    flex-basis: 30%;
}

.pc-container h2 {
    padding-top:20px;
    padding-bottom:5px;
    padding-left:4%;
}

.pc-item img {
    max-width:100%;
    transition:.5s;
}

.pc-item img:hover {
    transform:scale(1.1);
}

.pc-item .image-container{
    overflow:hidden;
}

.pc-item a {
    text-decoration: none;
    color:black;
}


