.projects-header {
    height:365px;
    width:100%;
    background-image: url("business-team-meeting-project-planning-concept-66696000.jpg");
    background-position-y:bottom;
    background-size: cover;
}

.projects-header h1 {
    color:white;
    font-weight: lighter;
}

.overlay-projects-header {
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height:100%;
    display: flex;
    flex-direction: column;
}

.h1-and-navbar-projects {
    padding:15px;
    width:60%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}

.nav-bar-projects a { 
    text-decoration: none;
    color:rgb(212, 211, 211);
    padding:0 15px;
    font-size: small;
}

.projects-header .nav-bar-projects > a:focus, a:hover {
    color:white;
}

.my-projects {
    text-align: center;
    position:relative;
    top:80px;
}